<script>
const HeaderTab = () => import("@/components/HeaderTab")
const MySwiper = () => import("@/components/Swiper")
export default {
    name: "cartoon",
    components: {
        MySwiper,
        HeaderTab,
    },
    data() {
		return {
			cartoonCarouselList: []
		}
	},
	mounted() {
		this.getCartoonCarouselList();
	},
	methods: {
        // 获取漫画轮播
		getCartoonCarouselList() {
			this.axios({
				url: "/api/carousel",
				headers: {
					'channelid': '395842662903689216',
					"sourceid": 2,
					"X-Tingyun-Id": "d1VZoc7h8h4;r=26118559"
				},
			}).then(res => {
				// console.log(res.data)
				if(res.data.code === 200) {
                    this.cartoonCarouselList = res.data.data;
				}
			})
		}
	},
}
</script>

<style scoped>
#cartoon {
